/* CartPage.css */

.cart-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.cart-info {
    text-align: center;
    margin-bottom: 20px;
}

.cart-container {
    display: flex;
    flex: 1;
    margin-right: 40px;
}

.cart-items {
    flex: 0 0 70%;
}

.card-container {
    display: flex;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin-bottom: 20px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
}

.card-container:hover {
    transform: scale(1.03);
}

.card-image {
    flex: 0 0 30%;
    max-height: 150px;
    overflow: hidden;
}

.card-image img {
    width: 100%;
    height: auto;
    border-radius: 8px 0 0 8px;
    object-fit: cover;
}

.card-details {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.card-details h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #333;
}

.card-details p {
    font-size: 1.2rem;
    margin-bottom: 10px;
    color: #666;
}

.cart-summary {
    flex: 0 0 30%;
    margin-left: 30px;
}

.cart-summary h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
}

.cart-summary hr {
    border: 1px solid #ddd;
    margin-bottom: 1rem;
}

.cart-summary h4 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.cart-address {
    margin-bottom: 1rem;
}

.cart-address h4 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.cart-address h5 {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.cart-address button {
    background-color: #ffc107;
    color: #000;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s;
}

.cart-address button:hover {
    background-color: #e0a800;
}

.cart-payment {
    margin-top: 2rem;
}

.cart-payment button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 0.8rem 2rem;
    cursor: pointer;
    border-radius: 4px;
    font-size: 1.2rem;
    transition: background-color 0.3s;
}

.cart-payment button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.cart-payment button:hover {
    background-color: #0056b3;
}

.cart-remove-btn button {
    background-color: #e93939; /* Change the background color to green */
    color: #fff;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s;
    font-size: 1rem;
}

.cart-remove-btn button:hover {
    background-color: #b52061; /* Change the background color on hover to a darker green */
}