section {
    display: flex;
    gap: 10px;
    cursor: pointer;
}
article {
    position: relative;
    overflow: hidden;
    background: #ffffff;
    width: 110px;
    height: 500px;
    border-radius: 36px;
    display: flex;
    align-items: center;
    opacity: 0.9;
    transition: 0.5s;
}

h2, p {
    margin: 0;
}

article.active {
    width: 400px;
    opacity: 1;
}

article h2 {
    font-size: 24px;
    font-weight: 400;
    color: rgb(255 255 255 / 96%);
}

article p {
    color: rgb(255 255 255 / 66%);
}

article .material-symbols-outlined {
    display: grid;
    place-items: center;
    width: 50px;
    height: 50px;
    background: rgb(255 255 255 / 80%);
    border-radius: 50%;
    font-size: 28px;
}

article img {
    position: absolute;
    z-index: 1;
    top:50%;
    left: 50%;
    translate: -50% -50%;
    height: 100%;
    /* filter: grayscale(0.5); */
}

article .content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 400px;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    padding: 100px 0 20px 20px;
    display: flex;
    align-items: center;
    gap: 14px;
    background: linear-gradient(
     to bottom,
    rgb(0 0 0 / 0%),
    rgb(0 0 0 / 80%)
    );
}

article.active .content {
    opacity: 1;
    visibility: visible;
}


/*....................*/
.animate-charcter
{
    text-transform: uppercase;
    background-image: linear-gradient(
            -225deg,
            /* #231557 0%,
            #44107a 29%,
            #ff1361 67%,
            #fff800 100% */

        
            #ff1361 67%,
            #fff800 100%
    );
    background-size: auto auto;
    background-clip: border-box;
    background-size:  200% auto;
    color: #fff;
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 2s linear infinite;
    display: inline-block;
    font-size: 38px;
}

@keyframes textclip {
    to {
        background-position: 200% center;
    }
}

