.my_footer{
    /*margin: 0;*/
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mukta', sans-serif;
    margin-top: auto;
}
.footer_body {
    display: flex;
    justify-content: center;
    align-items: flex-end;
     min-height: auto;
    background: #ffffff;
}

footer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background: #008000;
    height: 190px;
    padding: 20px 50px;
    flex-direction: column;
}

footer .footer_social-icons, 
footer .footer_manu {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
    flex-wrap: wrap;
    flex-direction: row; 
}

footer .footer_social-icons li , 
footer .footer_manu li {
    list-style: none;
}

footer .footer_social-icons li a{
    font-size: 2em;
    color: #ffffff;
    margin: 0 10px;
    display: inline-block;
    transition: 0.5s;
}

footer .footer_social-icons li a:hover{
    transform: translateY(-10px);
}

footer .footer_manu li a {
    font-size: 1.2em;
    color: #ffffff;
    margin: 0 10px;
    display: inline-block;
    text-decoration: none;
    opacity: 0.75;
    transition: 0.5s;
}

footer .footer_manu li a:hover{
    opacity: 1;
}

footer p {
    color: #ffffff;
    text-align: center;
    margin-top: 15px;
    margin-bottom: 10px;
    font-size: 1.1em;
    opacity: 0.75;
}

footer .wave {
    position: absolute;
    top: -100px;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('../../assets/wave1.png');
    background-size: 1000px 100px;
}

footer .wave#wave1 {
    z-index: 1000;
    opacity: 1;
    bottom: 0;
    animation: animateWave 4s linear infinite;
}

footer .wave#wave2 {
    z-index: 999;
    opacity: 0.5;
    bottom: 0;
    animation: animateWave-02 4s linear infinite;
}

footer .wave#wave3 {
    z-index: 1000;
    opacity: 0.2;
    bottom: 15px;
    animation: animateWave-02 3s linear infinite;
}

footer .wave#wave4 {
    z-index: 999;
    opacity: 0.7;
    bottom: 20px;
    animation: animateWave-02 3s linear infinite;
}

@keyframes animateWave {
    0% {
        background-position-x: 1000px;
    }
    100% {
        background-position-x: 0px;
    }
}

@keyframes animateWave-02 {
    0% {
        background-position-x: 0px;
    }
    100% {
        background-position-x: 1000px;
    }
}


  