.event_main {
margin: 0;
padding: 0;
box-sizing: border-box;
font-family: 'Montserrat', sans-serif;
}

event-body {
min-height: 100vh;
width: 100%;
font-size: 16px;
background: #f5f5f5; /* Light gray background */
color: #333; /* Dark text color */
display: flex;
justify-content: center;
align-items: center;

}

.event-container {
width: 100%;
display: flex;
justify-content: center;
align-items: center;
flex-wrap: wrap;
}

.event-item-container {
position: relative;
margin: 24px;
width: 320px;
height: 570px;
overflow: hidden;
background-color: #fff; /* White background for cards */
box-shadow: 0 0 30px 5px rgba(0, 0, 0, 0.15);
cursor: pointer;
}

.event-img-container,
.event-body-container {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.event-img-container img {
width: 100%;
height: 100%;
object-fit: cover; /* Ensure the image covers the container */
}

.event-body-container {
position: relative;
}

.event-overlay {
position: relative;
width: 100%;
height: 100%;
background-color: rgba(118, 163, 87, 0.6); /* Olive green overlay color */
opacity: 0;
transition: height linear 0.4s, opacity linear 0.2s;
}

.event-item-container:hover .event-overlay {
opacity: 1;
height: 150px;
}

.event-info {
background-color: #fff;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
padding: 0;
margin: 0;
}

.event-info-title,
.event-info-price {
color: #593a29; /* Brown color for title and price */
font-size: 1.5em;
font-weight: bold;
letter-spacing: 1px;
margin: 5px;
}

.event-info-info {
letter-spacing: 0.5px;
margin-bottom: 2px;
}

.event-info-separator {
width: 20%;
height: 6px;
background-color: #593a29; /* Brown color for separator */
margin-bottom: 8px;
}

.event-additional-info {
border-top: 1px solid #bbb;
margin-top: 12px;
padding: 20px;
padding-bottom: 0;
}

.event-additional-info-info {
font-size: 0.9em;
margin-bottom: 18px;
text-align: center;
}

.event-info i {
color: #593a29; /* Brown color for icons */
font-size: 1.1em;
margin-right: 4px;
}
.event-info i::before {
    content: ""; /* Empty content to remove the symbol */
    color: #593a29; /* Brown color for icons */
    font-size: 1.1em;
    margin-right: 4px;
}
.event-info span {
color: #593a29; /* Brown color for span */
font-weight: bolder;
}

.event-action {
color: #fff;
border: 3px solid #fff;
background-color: #593a29; /* Brown color for action button */
position: absolute;
top: -100px;
left: 50%;
transform: translateX(-50%);
width: 60%;
outline: none;
cursor: pointer;
padding: 12px;
text-transform: uppercase;
font-size: 1.3em;
font-weight: bold;
letter-spacing: 2px;
transition: background-color 0.4s, top 0.4s;
}

.event-item-container:hover .event-action {
top: 50px;
}

.event-action:hover {
background-color: #401e0a; /* Darker brown color on hover */
}

/* Additional styles for Tech Summit 2024 card */
.event-container .event-item-container:nth-child(2) .event-overlay {
background-color: rgba(118, 163, 87, 0.6); /* Olive green overlay color for Tech Summit */
}

.event-container .event-item-container:nth-child(2) .event-info-title,
.event-container .event-item-container:nth-child(2) .event-info-price {
color: #593a29; /* Brown color for Tech Summit title and price */
}

.event-container .event-item-container:nth-child(2) .event-info-separator {
background-color: #593a29; /* Brown color for Tech Summit separator */
}

.event-container .event-item-container:nth-child(2) .event-action {
border: 3px solid #593a29; /* Brown border for Tech Summit action button */
}

/* Additional styles for Harvest Festival 2024 card */
.event-container .event-item-container:nth-child(3) .event-overlay {
background-color: rgba(130, 175, 71, 0.6); /* Light green overlay color for Harvest Festival */
}

.event-container .event-item-container:nth-child(3) .event-info-title,
.event-container .event-item-container:nth-child(3) .event-info-price {
color: #825a47; /* Darker brown color for Harvest Festival title and price */
}

.event-container .event-item-container:nth-child(3) .event-info-separator {
background-color: #825a47; /* Darker brown color for Harvest Festival separator */
}

.event-container .event-item-container:nth-child(3) .event-action {
border: 3px solid #825a47; /* Darker brown border for Harvest Festival action button */
}

/* Responsive styles */
@media (max-width: 768px) {
.event-item-container {
    width: 100%;
    max-width: 400px;
}
}