/* team member */

.team_body {
    background: #e8cbc0;
    background: -webkit-linear-gradient(to right, #e8cbc0, #54a48b);
    background: linear-gradient(to right, #f1eeee, #56b68b);
    /* min-height: 100vh; */
}

.social-link {
    width: 30px;
    height: 30px;
    border: 1px solid #ddd;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #666;
    border-radius: 50%;
    transition: all 0.3s;
    font-size: 0.9rem;
    font-family: "Font Awesome 5 Free";
}

.social-link:hover, .social-link:focus {
    background: #ddd;
    text-decoration: none;
    color: #555;
}

.Contact_button {
    display: inline-block;
    width: 1300px;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #4CAF50; /* Green color */
    color: #ffffff; /* White text */
    transition: background-color 0.3s ease;
  }
  
  .Contact_button:hover {
    background-color: #45a049; /* Darker green color on hover */
  }