* {
    margin: 0;
    padding: 0;
}

.sky {
    height: 100vh;
    background-image: url("../assets/s.png");
    width: 100%;
    background-size: cover;
    position: relative;
    overflow-x: hidden;
}

.grass {
    height: 600px;
    width: 300%;
    display: block;
    background-image: url("../assets/gh.png");
    position: absolute;
    bottom: 0;
    left: -2400px;
    right: 0;
    overflow-y: hidden;
    background-repeat: repeat-x;
    animation: grass 15s linear infinite;
}
@keyframes grass {
    100% {
        transform: translateX(2400px);
    }
}

.mountain {
    height: 500px;
    width: 500%;
    display: block;
    background-image: url("../assets/m.png");
    position: absolute;
    top: 200px;
    bottom: 0;
    left: -1400px;
    right: 0;
    overflow-y: hidden;
    background-repeat: repeat-x;
    animation: mountain 30s linear infinite;
}
@keyframes mountain {
    100% {
        transform: translateX(1400px);
    }
}

.farmer {
    z-index: 3;
    position: absolute;
    bottom: 0;
    left: 40%;
    top: 64.5%;
}

