.title {
    font-size: 58px;
    text-align: center;
    margin-top: 30px;
    font-style: italic;
    position: relative;
    display: inline-block;
    color: goldenrod; /* Set your desired text color */
    animation: pulse 1s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 0.8;
    }
    50% {
        transform: scale(1.05);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 0.8;
    }
}

.text {
    font-size: 25px;
    text-align: center;
    margin-top: 28px;
    font-style: italic;
}

.money {
    z-index: 2;
    position: absolute;
    bottom: 0;
    /*left: 30%;*/
    right: 0;
    top: 45%;
}

.planting {
    z-index: 3;
    position: absolute;
    bottom: 0;
    left: 0;
    /*right: 90%;*/
    top: 48%;
}
.pickingFruits {
    z-index: 5;
    position: absolute;
    bottom: 0;
    left: 24%;
    top: 51%;
}


