/* AboutUs.css */

.about-us-container {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
 h2 {
    color: #333;
    text-align: center;
  }
  h1{
    color: #1c1717;
    font-weight: bold;
    text-align: center;
    font-size: 60px;
  }
  p {
    line-height: 1.6;
    color: #666;
  }
  
  .team-members {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .team-member {
    flex: 0 0 calc(30% - 20px);
    text-align: center;
    margin-bottom: 20px;
    padding: 15px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
  }
  
  .team-member:hover {
    transform: translateY(-5px);
  }
  
  .team-member img {
    width: 100%;
    max-width: 150px;
    height: auto;
    border-radius: 50%;
  }
  
