/* Add this CSS to your shop.css file or any styling file you are using */

/* Shop Container */
.shop-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  
  /* Filter Section */
  .filter-section {
    flex: 1;
    max-width: 300px; /* Set a max-width to limit the size */
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 8px;
  }
  
  .filter-section h4 {
    margin-bottom: 10px;
  }
  
  .filter-section button {
    margin-top: 10px;
  }
  
  /* Product Section */
  .product-section {
    flex: 2;
    padding: 20px;
  }
  
  .product-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  /* Product Card */
  .product-card {
    border: 1px solid #ddd;
    padding: 10px; /* Adjust padding for a smaller size */
    text-align: center;
    transition: transform 0.3s ease-in-out;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
  }
  
  .product-card:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  }
  
  .product-card img {
    width: 100%;
    height: 150px; /* Set a fixed height for the product image */
    object-fit: cover; /* Maintain aspect ratio and cover the container */
    border-radius: 8px;
  }
  
  .product-card div {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
  }
  
  .product-card h5 {
    margin: 10px 0;
    font-size: 1.2em; /* Adjust font size for a smaller size */
    color: #333;
  }
  
  .product-card p {
    color: #666;
    margin: 5px 0;
  }
  
  .product-card button {
    background-color: #4caf50;
    color: white;
    padding: 5px 10px; /* Adjust padding for a smaller size */
    border: none;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease-in-out;
    margin-top: 5px; /* Add margin-top to create space between buttons */
  }
  
  .product-card button:hover {
    background-color: #45a049;
  }
  
  .product-card .button-container {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
  }
  
  /* Load More Button */
  .load-more-btn,
  .reset-filters-btn {
    background-color: #3498db;
    color: white;
    padding: 5px 10px; /* Adjust padding for a smaller size */
    border: none;
    cursor: pointer;
    border-radius: 4px;
    margin-top: 10px;
    transition: background-color 0.3s ease-in-out;
  }
  
  .load-more-btn:hover,
  .reset-filters-btn:hover {
    background-color: #2980b9;
  }

  .product-card .rating {
    display: flex;
    justify-content: center;
    margin: 5px 0;
  }
  
  .product-card .rating span {
    font-size: 1.2em;
    color: #f8d757; /* Change the color as needed */
    margin: 0 2px;
    transition: transform 0.3s ease-in-out;
  }
  
  .product-card:hover .rating span {
    transform: scale(1.2); /* Add a hover effect */
  }

/* ------------------------------------------------------------------------------------------ */
.star-rating {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    margin: 0;
  }
  
  .radio-input {
    position: fixed;
    opacity: 0;
    pointer-events: none;
  }
  
  .radio-label {
    cursor: pointer;
    font-size: 0;
    color: rgba(0,0,0,0.2);
    transition: color 0.1s ease-in-out;
  }
  
  .radio-label:before {
    content: "";
    display: inline-block;
    font-size: 32px;
  }
  
  .radio-input:checked ~ .radio-label {
    color: #ffc700;
    color: gold;
  }
  
  .radio-label:hover,
  .radio-label:hover ~ .radio-label {
    color: gold;
  }
  
  .radio-input:checked + .radio-label:hover,
  .radio-input:checked + .radio-label:hover ~ .radio-label,
  .radio-input:checked ~ .radio-label:hover,
  .radio-input:checked ~ .radio-label:hover ~ .radio-label,
  .radio-label:hover ~ .radio-input:checked ~ .radio-label {
    color: rgb(126, 184, 11);
  }
  
  
  .average-rating {
    position: relative;
    appearance: none;
    color: transparent;
    width: auto;
    display: inline-block;
    vertical-align: baseline;
    font-size: 25px;
  }
  
  .average-rating::before {
    --percent: calc(4.3/5*100%);
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    color: rgba(0,0,0,0.2);
    background: linear-gradient(90deg, gold var(--percent), rgba(0,0,0,0.2) var(--percent));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  
  

  