/* EmailRestAPI.css */

.email-container {
  max-width: 600px;
  margin: 0 auto;
}

.submit-btn,
.dashboard-btn {
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  margin-right: 10px;
}

.submit-btn:hover,
.dashboard-btn:hover {
  background-color: #17a2b8;
  transform: scale(1.1);
}
