.policy-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    transform-origin: center bottom;

  }
  
  .policy-container:hover {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
    transform: scale(1.02);
  }
  
  .policy-title {
    color: #3498db;
    text-align: center;
    margin-bottom: 20px;
    font-size: 28px;
  }
  
  .last-updated {
    font-size: 14px;
    color: #777;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .policy-intro {
    font-size: 16px;
    line-height: 1.6;
    color: #333;
  }
  
  .section-title {
    font-size: 24px;
    color: #3498db;
    margin-top: 20px;
  }
  
  .section-content {
    font-size: 16px;
    line-height: 1.6;
    color: #555;
  }
  
  .section-content a {
    color: #3498db;
    text-decoration: underline;
    transition: color 0.3s ease;
  }
  
  .section-content a:hover {
    color: #1e618d;
  }
  
  .section-content p {
    margin-bottom: 15px;
  }
  
  .section-content br {
    display: none; /* Hide line breaks to avoid excessive spacing */
  }
  
  /* Additional Styles */
  
  /* Gradient Background */
  .policy-container {
    background: linear-gradient(to bottom, #ffffff, #f8f8f8);
  }
  
  /* Rounded Corners for Images */
  .policy-container img {
    border-radius: 8px;
    max-width: 100%;
  }
  
  /* Responsive Video Embedding */
  .section-content iframe {
    width: 100%;
    height: 400px; /* Adjust height as needed */
    border: 0;
  }
  
