/* Updated CSS with card image and content */
.cat_row {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .cat_card {
    flex: 0 0 calc(20% - 20px);
    max-width: calc(20% - 20px);
    position: relative;
    overflow: hidden;
    border: 1px solid #ddd;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    cursor: pointer;
    background: linear-gradient(to bottom, #ffffff, #f9f9f9);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
  }
  
  .cat_image {
    max-width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
  }
  
  .cat_content {
    padding: 15px;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .cat_title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .cat_button {
    display: inline-block;
    padding: 10px 15px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    color: #fff;
    background-color: #4caf50;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
  }
  
  .cat_button:hover {
    background-color: #45a049;
    color: #fff;
  }
  
  .cat_text {
    font-weight: bold;
  }