.pnf_body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f0f0;
}
.pnf_error-container {
    text-align: center;
    opacity: 0; /* Initial opacity set to 0 for the animation */
    animation: fadeIn 0.5s ease-in-out forwards; /* Apply the animation */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.pnf_error-content {
    background-color: #fff;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.pnf_error-content h1 {
    font-size: 3em;
    color: #e74c3c;
    margin-bottom: 10px;
}

.pnf_error-content p {
    font-size: 1.2em;
    color: #333;
    margin-bottom: 20px;
}

.pnf_btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #e74c3c;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.pnf_btn:hover {
    background-color: #c0392b;
}
.pnf_error-circle {
    display: inline-block;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: #e74c3c;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    animation: circleAnimation 2s infinite;
}

.pnf_error-text {
    font-size: 3em;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@keyframes circleAnimation {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}
