@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

tricker_a {
    color: #fff;
    text-decoration: none;
}

tricker_body {
    height: calc(100vh);
    font-family: 'Poppins', sans-sherif;
    font-weight: 100;
    display: flex;
    color: #fff;
    align-items: top;
    justify-content: center;
    overflow: hidden;
}

.tricker_ticker {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 0px;
    
}
.tricker_news {
    width: 80%;
    height: 8px;
    /* background: linear-gradient(45deg, #00ff00, #008000); */
   
}
.tricker_title {
    width: 20%;
    height: 30px;
    text-align: center;
    background: radial-gradient(circle at 100% 50%, rgba(255, 69, 0, 0.7) 0%, rgba(178, 34, 34, 0.7) 100%); /* Radial gradient covering half a circle from right to left */
    position: relative;
    color: #ffffff;
    border-top-right-radius: 15px; /* Half of the height to create a half-circle effect */
    border-bottom-right-radius: 15px;
}
.tricker_title:after {
    position: absolute;
    content: "";
    right: -12%;
    top: 0;
}
.tricker_title h5 {
    font-size: 14px;
    margin: 3% 0;
    font-weight: bold; 
}
.tricker_news marquee {
    font-size: 13.5px;
    color: #000000;
    font-weight: bold; 
}