** {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .testimonial-wrapper {
    background: linear-gradient(90deg, #00a86b 0%, #00ff95 100%);
    min-height: 100vh;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'raleway', sans-serif;
    color: #fff;
  }
  
  .testimonial-wrapper h1 {
    font-size: 55px;
    font-weight: 400;
    margin-top: 30px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .testimonial-wrapper h5 {
    font-size: 20px;
    text-align: center;
    font-weight: 300;
    margin-bottom: 80px;
  }
  
  .testimonial-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1200px;
    margin: 0 auto;
  }
  
  .testimonial-card {
    width: 380px;
    height: 500px;
    border-radius: 15px;
    margin: 7px;
    padding: 20px 30px;
    position: relative;
    background: rgba(255, 255, 255, 0.2);
    box-shadow: 10px 20px 25px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
  }
  
  .testimonial-card img {
    height: 90px;
    width: 90px;
    border: 2px solid #f2f2f2;
    border-radius: 50%;
    position: absolute;
    top: -40px;
    left: 20px;
  }
  
  .testimonial-card p {
    font-size: 14px;
    margin-top: 40px;
    margin-bottom: 25px;
  }
  
  .testimonial-card h2 {
    font-weight: 500;
    margin-bottom: 4px;
  }
  
  .testimonial-card h4 {
    color: #333;
    font-weight: 400;
    font-size: 14px;
  }
  
  .testimonial-card i {
    font-size: 10px;
    position: absolute;
    bottom: 20px;
    right: 25px;
    color: #b2bec3;
  }
  
  .testimonial-card:hover {
    transform: scale(1.4);
    color: #333;
    background: #fff;
    z-index: 10000;
  }
  
  @media (max-width: 768px) {
    .testimonial-cards {
        width: 100%;
        flex-direction: column;
        flex-wrap: wrap;
    }
    .testimonial-card {
        margin: 40px 0;
    }
  }