  /* Your existing styles */

.button-container {
    display: flex;
    justify-content: right;
    margin-top: 0;
  }
  
  .theme-toggle-button {
    display: flex;
    align-items: center;
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
    border: none;
    outline: none;
    transition: background-color 0.3s ease, color 0.3s ease;
    border-radius: 20%;
  }
  
  .light-theme .theme-toggle-button {
    background-color: #3498db; /* Set the desired background color for light mode */
    color: #ffffff; /* Set the desired text color for light mode */
  }
  
  .dark-theme .theme-toggle-button {
    background-color: #2c3e50; /* Set the desired background color for dark mode */
    color: #ffffff; /* Set the desired text color for dark mode */
  }
  
