  /* Apply basic styles to the event container */
.event-container {
  background-color: #dfefe3;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Style the event header */
.event-header {
  text-align: center;
  margin-bottom: 20px;
}

/* Style the h2 element inside the event header */
.event-header h2 {
  color: #333;
  font-size: 40px;
}


/* weather */
.weather_body {
  font-family: 'Arial', sans-serif;
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 100vh;
      background-color: #f4f4f4;
    }

    .weather-card {
      background-color: #fff;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      border-radius: 8px;
      overflow: hidden;
      width: 100%;
      text-align: center;
    }

    .weather-icon {
      margin-top: 20px;
    }

    .temperature {
      font-size: 32px;
      font-weight: bold;
      margin: 15px 0;
    }

    .location {
      color: #555;
      font-size: 18px;
      margin-bottom: 10px;
    }

    .description {
      color: #777;
      margin-bottom: 20px;
    }

    .download-button {
      background-color: #3498db;
      color: #fff;
      padding: 10px;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      text-decoration: none;
      display: inline-block;
    }

    /* team member */

    .team_body {
      background: #e8cbc0;
      background: -webkit-linear-gradient(to right, #e8cbc0, #54a48b);
      background: linear-gradient(to right, #f1eeee, #56b68b);
      /* min-height: 100vh; */
  }
  
  .social-link {
      width: 30px;
      height: 30px;
      border: 1px solid #ddd;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #666;
      border-radius: 50%;
      transition: all 0.3s;
      font-size: 0.9rem;
      font-family: "Font Awesome 5 Free";
  }
  
  .social-link:hover, .social-link:focus {
      background: #ddd;
      text-decoration: none;
      color: #555;
  }
 
  .mynew_container {
    display: flex;
  }

  .right-column {
    flex: 1;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 10px;
    margin: 0;
  }

  .left-column {
    flex: 2;
    max-width: 100%;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 20px;
    margin: 20px;
    text-align: justify;
    overflow: hidden;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f5f5f5;
    color: #333;
  }

  .myh1 {
    /* color: #1e88e5; */
    color: #202cb3;
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: bold;
  }

  .myh2 {
    color: #000000;
    margin-top: 25px;
    font-size: 24px;
    font-weight: bold;
  }

  .myp {
    line-height: 1.8;
    margin-bottom: 15px;
    font-size: 16px;
  }

  .report_botton {
    width: 100%;
    background: linear-gradient(90deg,  #404040 0%, #202cb3 50%, #6d5fea 100%);
    color: white;
    border: none;
    padding: 12px 25px;
    font-size: 18px;
    border-radius: 30px;
    cursor: pointer;
    margin-top: 50px;
}

.report_botton:hover {
    background: linear-gradient(90deg, #404040 0%, #005580 50%, #00a6e6 100%);
}

